.page {
  background-image: url("../../assets/img/graphicBack3.jpg");
  /* background-color: #3c3c38; */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  overflow: hidden;
}

@keyframes cardDraw {
  0% {
    transform: scale(1) rotate(0deg);
  }
  50% {
    transform: scale(1.5) rotate(15deg);
  }
  100% {
    transform: scale(1) rotate(0deg);
  }
}

@keyframes cardRemove {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(0.5);
  }
}

.animate-card-draw {
  animation: cardDraw 3s ease-in-out; /* 카드 뽑히는 애니메이션 */
}

.animate-card-remove {
  animation: cardRemove 1s ease-in-out forwards; /* 카드 사라지는 애니메이션 */
}

.dask {
  transform: perspective(150px) rotateX(7deg);
}
